import { motion, AnimatePresence } from 'framer-motion'
import styled from 'styled-components'
import { useCanvas } from '../context/CanvasContext'

const CardsContainer = styled.div`
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
`

const Card = styled(motion.div)`
  position: absolute;
  min-width: 220px;
  padding: 1rem;
  border-radius: 12px;
  background: rgba(255, 255, 255, 0.95);
  border: 1px solid rgba(0, 0, 0, 0.1);
  color: #333;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  transition: transform 0.2s, box-shadow 0.2s;
  backdrop-filter: blur(8px);
  font-size: 14px;
  font-weight: 500;

  &:hover {
    transform: scale(1.05);
    box-shadow: 0 12px 40px rgba(0, 0, 0, 0.15);
  }

  &::before {
    content: '🔍';
    margin-right: 8px;
    opacity: 0.7;
  }
`

const calculateOrbitPosition = (index: number, total: number, radius: number) => {
  const angle = (index / total) * Math.PI * 2
  return {
    x: Math.cos(angle) * radius,
    y: Math.sin(angle) * radius
  }
}

export const SuggestionCards = () => {
  const { suggestions, currentText } = useCanvas()
  const radius = 200 // Orbit radius in pixels

  return (
    <CardsContainer>
      <AnimatePresence mode="wait">
        {currentText && suggestions.map((suggestion, index) => {
          const position = calculateOrbitPosition(index, suggestions.length, radius)
          return (
            <Card
              key={suggestion.id}
              initial={{ 
                opacity: 0,
                x: 0,
                y: 0,
                scale: 0.8
              }}
              animate={{ 
                opacity: 1,
                x: position.x,
                y: position.y,
                scale: 1
              }}
              exit={{ 
                opacity: 0,
                x: 0,
                y: 0,
                scale: 0.8
              }}
              transition={{ 
                duration: 0.3,
                delay: index * 0.1
              }}
            >
              {suggestion.text}
            </Card>
          )
        })}
      </AnimatePresence>
    </CardsContainer>
  )
} 