import { createContext, useContext, useState, ReactNode, useEffect } from 'react'

interface Suggestion {
  id: number
  text: string
}

interface CanvasContextType {
  particles: any[]
  setParticles: (particles: any[]) => void
  currentText: string
  setCurrentText: (text: string) => void
  suggestions: Suggestion[]
}

const CanvasContext = createContext<CanvasContextType>({
  particles: [],
  setParticles: () => {},
  currentText: '',
  setCurrentText: () => {},
  suggestions: []
})

export const useCanvas = () => useContext(CanvasContext)

// Mock suggestions based on input patterns
const getSuggestions = (text: string): Suggestion[] => {
  // Default suggestions when no text
  if (!text) {
    return [
      { id: 1, text: 'Search for recipes, places, or topics...' },
      { id: 2, text: 'Try "food", "travel", or "code"' },
      { id: 3, text: 'Type to see search suggestions' }
    ]
  }

  // Food-related suggestions
  if (text.startsWith('food')) {
    if (text.startsWith('food po')) {
      return [
        { id: 1, text: 'Search: food poisoning symptoms' },
        { id: 2, text: 'Search: food portion calculator' },
        { id: 3, text: 'Search: food poverty solutions' }
      ]
    } else if (text === 'food portrait') {
      return [
        { id: 1, text: 'Search: food photography techniques' },
        { id: 2, text: 'Search: food art galleries' },
        { id: 3, text: 'Search: food styling tutorials' }
      ]
    } else {
      return [
        { id: 1, text: 'Search: easy dinner recipes' },
        { id: 2, text: 'Search: seasonal food guide' },
        { id: 3, text: 'Search: local restaurants' }
      ]
    }
  }

  // Travel-related suggestions
  if (text.startsWith('travel')) {
    if (text.startsWith('travel ba')) {
      return [
        { id: 1, text: 'Search: travel packing list' },
        { id: 2, text: 'Search: best travel bags' },
        { id: 3, text: 'Search: travel banking tips' }
      ]
    } else if (text === 'travel backpack') {
      return [
        { id: 1, text: 'Search: best travel backpacks' },
        { id: 2, text: 'Search: backpack packing guide' },
        { id: 3, text: 'Search: backpack vs suitcase' }
      ]
    } else {
      return [
        { id: 1, text: 'Search: best travel destinations' },
        { id: 2, text: 'Search: budget travel tips' },
        { id: 3, text: 'Search: travel photography' }
      ]
    }
  }

  // Code-related suggestions
  if (text.startsWith('code')) {
    if (text.startsWith('code re')) {
      return [
        { id: 1, text: 'Search: code refactoring patterns' },
        { id: 2, text: 'Search: code review checklist' },
        { id: 3, text: 'Search: regression testing guide' }
      ]
    } else if (text === 'code review') {
      return [
        { id: 1, text: 'Search: code review tools' },
        { id: 2, text: 'Search: code review best practices' },
        { id: 3, text: 'Search: code review automation' }
      ]
    } else {
      return [
        { id: 1, text: 'Search: programming languages' },
        { id: 2, text: 'Search: coding tutorials' },
        { id: 3, text: 'Search: code editors' }
      ]
    }
  }

  // Generic suggestions for any other text
  return [
    { id: 1, text: `Search: ${text} tutorial` },
    { id: 2, text: `Search: best ${text}` },
    { id: 3, text: `Search: ${text} guide` }
  ]
}

export const CanvasProvider = ({ children }: { children: ReactNode }) => {
  const [particles, setParticles] = useState<any[]>([])
  const [currentText, setCurrentText] = useState('')
  const [suggestions, setSuggestions] = useState<Suggestion[]>([])

  useEffect(() => {
    setSuggestions(getSuggestions(currentText))
  }, [currentText])

  return (
    <CanvasContext.Provider 
      value={{ 
        particles, 
        setParticles, 
        currentText, 
        setCurrentText, 
        suggestions 
      }}
    >
      {children}
    </CanvasContext.Provider>
  )
} 